@import "../../../static/images/logo/index.css";
/*@font-face {
    font-family: myFont;
    src: url("./0014-BYBlueStarship-2.ttf");
}*/
.myFont{
    font-family: myFont, serif;
}
.logo{
    -webkit-user-select: none;  /* 禁止 DIV 中的文本被鼠标选中 */
    -moz-user-select: none;     /* 禁止 DIV 中的文本被鼠标选中 */
    -ms-user-select: none;      /* 禁止 DIV 中的文本被鼠标选中 */
    user-select: none;
    padding-top: 10px;
    border-bottom:3px solid #003057;
    padding-bottom:3px;
    padding-left: 10px;
}
.logo img {
    width: 180px;;
}
/*header头部布局*/
.Header_container{
    padding: 0px 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.headerTag{
    max-width: 80px;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-align: center;
}
.user_action_container,.user_info_container{
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.user_action_button{
    background-color: #00474f;
    color: #c9c9c9;
}
.current_notebook_name{
    margin-top: 2px;
    margin-left: 10px;
    font-size: 17px;
    font-weight: 600;
    line-height: 10px;
    color: #dedede;
}
.user_info_container{
    -webkit-user-select: none;  /* 禁止 DIV 中的文本被鼠标选中 */
    -moz-user-select: none;     /* 禁止 DIV 中的文本被鼠标选中 */
    -ms-user-select: none;      /* 禁止 DIV 中的文本被鼠标选中 */
    user-select: none;             /* 禁止 DIV 中的文本被鼠标选中 */
    width: 300px;
    color: white;
}
/*content样式*/
.content_container{
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    border:#030852 2px solid;
}
.scroll{
    position: absolute;
    width: 5px;
    /*移入光标*/
    cursor: col-resize;
    z-index: 100;
}
.left_container{
    /*background-color: #1d3c41;*/
    min-width: 250px;
}
.left_container,.right_container,.scroll{
    height: 100%;
    min-height: 635px;
    overflow: auto;
}
.LeftNavMenu{
    -webkit-user-select: none;  /* 禁止 DIV 中的文本被鼠标选中 */
    -moz-user-select: none;     /* 禁止 DIV 中的文本被鼠标选中 */
    -ms-user-select: none;      /* 禁止 DIV 中的文本被鼠标选中 */
    user-select: none;             /* 禁止 DIV 中的文本被鼠标选中 */
}
/*左侧notebook导航栏的页头样式*/
.NoteListNavHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    padding: 0 10px;
    height: 50px;
}
.NoteNavList{
    /*padding: 0 5px;*/
    padding-right: 5px;
}
.NoteNavListItem:hover{
    background-color: #e6f7ff;
}
.Editor_Header{
    display: flex;
    flex-direction:column;
}
