.MyBraftEditor{
    display: flex;
    -webkit-user-select: none;  /* 禁止 DIV 中的文本被鼠标选中 */
    -moz-user-select: none;     /* 禁止 DIV 中的文本被鼠标选中 */
    -ms-user-select: none;      /* 禁止 DIV 中的文本被鼠标选中 */
    user-select: none;
    height: calc(100% - 120px);/* 禁止 DIV 中的文本被鼠标选中 */
}
.MarkDownContainer{
    max-height: 1200px;
    width:50%;
    backgroundColor:#ffffff;
    padding-left:10px;
}
.MarkDown{
    overflow: auto;
    height: 100%;
    max-width: 100%;
    background-color: #fafafa;
    padding:5px;
    border-radius: 5px;
    border:1px solid #188ffd;
}